.noise {
    background-image: url(data:image/png;base64,);
}

$dark: #212121;
$light: #fafafa;

// https://material.io/resources/color/
// 300 grey
$background-color: #e0e0e0;
// 400 grey
$color-on-light: #424242;
// 900 grey
$heading-color-on-light: #212121;

body {
    background-color: $background-color;
    color: $color-on-light;
    //margin-bottom: 1000rem;

    * {
        margin: 0;
    }
}

a {
    color: #42a5f5;
    transition: .2s;
    
    &:visited {
        color: #42a5f5;
    }
    
    &:active, &:hover {
        color: #1e88e5;
        text-decoration: none;
    }
}

#site-header-navbar {
    background-color: #fff;
    //color: $dark;
    padding: .2rem 1rem;
    display: flex;
    align-items: center;
    justify-items: stretch;
    position: fixed;
    width: 100%;
    height: 50px;
    box-shadow: 0px 0px 3px -1px #000;

    a {
        color: #757575;
        
        &:hover, &:active {
            color: #424242;
        }
    }

    h5 {
        flex: 1;

        a {
            color: #616161;
        
            &:hover, &:active {
                color: #212121;
            }
        }
    }

    > ul {
        flex: 3;
        list-style-type: none;
        text-align: right;

        * {
            display: inline;
        }

        > li {
            padding: 1rem 0;
        }

        > li + li {
            margin-left: 1rem;
        }

        /*
        > .site-header-navbar-list-dropdown {
            position: relative;
            display: inline;

            > ul {
                background-color: #fff;
                padding: .5rem 1rem;
                text-align: left;
                border: 1px solid #aaa;
                position: absolute;
                z-index: 2;
                display: none;

                > li {
                    white-space: pre;
                    display: block;
                }
            }

            &:hover ul{
                display: block;
            }
        }
        */
    }
}

#site-header-message {
    text-align: center;
    padding: .5rem 0;
    background-color: #212121;
    color: #fff;
}

#site-header-spacer {
    display: block;
    height: 50px;
    width: 100%;
}

#site-header-content {
    background-color: #212121;
    color: #fff;
    padding: 3rem;
    display: flex;
    align-items: center;
    //height: 100%;

    > * {
        //border: 1px solid #000;
        flex: 1;
    }

    img {
        //padding: 1rem;
        width: 100%;
        height: auto;
        //margin: 0 auto;
    }

}

#site-header-content-message {
    margin-right: 1rem;

    h2 {
        font-weight: bold;
        color: #fff;
        //line-height: 3.5rem;

        + p {
            margin: 2rem 0;
        }
    }
}

#get-started {
    display: inline-block;
    padding: .5rem 1rem;
    background-color: #fff;
    color: #212121;
    font-weight: bold;
    //border-radius: 5px;
    //border: 1px solid #000;

    &:hover, &:active {
        background-color: #9e9e9e;
        //color: #cfd8dc;
    }
}

#site-main {
    margin: 3rem 0;
}

.card {
    padding: 2rem 1rem;
    color: #616161;
    box-shadow: 0px 0px 3px -1px #000;
    margin: 0 5%;
    background-color: #fff;
    text-align: center;

    + .card {
        margin-top: 3rem;
    }

    h1, h2, h3, h4, h5, h6 {
        color: $heading-color-on-light;
    }


}

#feature-items {
    display: flex;
    justify-content: center;
    margin-top: 2.5rem;
}

.feature-item {
    flex: 1;
    //border: 1px solid #000;

    > img {
        /*
        Somehow,The margin doesn't look right without border
        */
        border: 1px solid transparent;
        margin: 0 auto;
        margin-bottom: 2rem;
        width: 200px;
        height: auto;
    }

    > h5 {
        //color: #333;
        margin: 1rem 0;
    }

    &:nth-child(2) {
        margin: 0 2rem;
    }
}

#blog-items {
    display: flex;
    margin-top: 2.5rem;
}

.blog-item {
    flex: 1;
    justify-content: center;
    text-align: left;
    img {
        margin-bottom: 2rem;
        width: 100%;
        max-height: auto;
    }

    h6 {
        line-height: 1.8rem;
    }

    p {
        font-size: 17px;
        //color: #555;
    }

    &:nth-child(2){
        margin: 0 2rem;
    }
}

#blog-subscribe {
    background-color: #1a237e;
    color: #fff;
    text-align: center;
    padding: 3rem 0;
    margin-top: 3rem;

    h4, a {
       color: inherit;
    }

    a {
        text-decoration: underline;
    }

    form {
        margin: 1rem 0;

        input {
            border: 1px solid #000;
            padding: .5rem 1rem;
        }
        input[type=email] {
            background-color: #fff;

            &:invalid {
                border: 1px solid red;
                box-shadow: none;
            }
        }
        input[type=submit] {
            transition: .2s;
            background-color: #fff;
            color: #000;

            &:focus, &:hover {
                background-color: #c5cae9;
            }
        }
    }
}

#templeos {
    border: 3px solid gold;
    background-color: #55FFFF;
    box-shadow: none;
    color: purple;

    h4 {
        color: purple;
    }

    img {
        max-width: 100%;
        height: auto;
        margin: 0 auto 1rem;
    }
}

footer {
    background-color: #263238;
    padding: 3rem 1rem;
    color: #999;
}

#footer-content {
    display: flex;
    margin-bottom: 1rem;

    > * {
        flex: 1;

        &:nth-child(2) {
            
        }
    }

    p, h6 {
        color: #fff;
    }

    h6 {
        margin-bottom: 1rem;
    }

    ul {
        list-style-type: none;
        float: left;
        width: 50%;
    }

    form {
        input, textarea {
            width: 100%;
            border: 1px solid #000;
            box-shadow: none;
            float: left;
            padding: .5rem;
        }

        input[type=text] {
            width: 40%;
        }

        input[type=submit] {
            transition: .2s;
            background-color: #fff;
            color: #000;

            &:focus, &:hover {
                background-color: #cfd8dc;
            }
        }

        input[type=email] {
            width: 58%;
            margin-left: 2%;

            &:invalid {
                border: 1px solid red;
                box-shadow: none;
            }
        }

        textarea {
            margin: 1rem 0;
        }
    }
}

#footer-content-nav {
    margin: 0 1rem;

    li {
        color: #fff;
    }
}


@media (min-width: 320px) and (max-width: 480px) {
    #site-header-content {
        flex-direction: column-reverse;
        padding: 3rem 1rem;

        h2 {
            font-size: 3rem;
            line-height: 3.8rem;
            //line-height: 3.7rem;
        }

        img {
            width: 100%;
            //max-height: auto;
            margin-bottom: 2rem;
            //order: 1;
            //align-self: flex-start;
        }
    }

    #site-main {
    }

    .card {
        padding: 2rem 1rem;
    }
    #feature-items, #blog-items {
        flex-direction: column;
    }

    .feature-item:nth-child(2) {
        margin: 3rem 0;
    }

    #templeos {
        h4 span {
            display: block;
        }
    }

    .blog-item:nth-child(2) {
        margin: 3rem 0;
    }

    #blog-subscribe {

        input[type=email] {
            width: 60%;
        }

        input[type=submit] {
            width: 30%;
        }
    }

    footer {
        padding: 3rem 1rem; 
    }

    #footer-content {
        flex-direction: column;
    }

    #footer-content-nav {
        margin: 2rem 0;
    }
}